.root {
	position: relative;
	left: -120px;
}
.input {
	border: 1px solid rgba(0, 0, 0, 0.1);
	border-radius: 15px;
	padding: 12px 40px;
	width: 300px;
	font-size: 16px;
	&:focus {
		border: 1px solid rgba(0, 0, 0, 0.2);
	}
}
.icon {
	width: 22px;
	height: 22px;
	opacity: 0.2;
	position: absolute;
	left: 14px;
	top: 12px;
}
.iconClear {
	width: 18px;
	height: 18px;
	opacity: 0.3;
	position: absolute;
	right: 15px;
	top: 15px;
    cursor: pointer;
    &:hover{
        opacity: 0.8;
    }
}
